.carddeck {
  width: calc(80% - 40px);
  margin-left: 40px;
  display: grid;
  grid-template-columns: repeat(4, minmax(260px, 1fr));
  grid-template-rows: repeat(4, minmax(180px, 1fr));
  gap: 20px;

  .card {
    &:nth-child(1) {
      background-color: red;
      grid-column: 1 / span 2;
      grid-row: 1 / span 1;
    }

    &:nth-child(2) {
      background-color: orange;
      grid-column: 1 / span 2;
      grid-row: 2 / span 1;
    }

    &:nth-child(3) {
      background-color: red;
      grid-column: 3 / span 1;
      grid-row: 1 / span 2;
    }

    &:nth-child(4) {
      background-color: orange;
      grid-column: 4 / span 1;
      grid-row: 1 / span 2;
    }

    &:nth-child(5) {
      background-color: red;
      grid-column: 1 / span 1;
      grid-row: 3 / span 2;
    }

    &:nth-child(6) {
      background-color: orange;
      grid-column: 2 / span 1;
      grid-row: 3 / span 2;
    }

    &:nth-child(7) {
      background-color: red;
      grid-column: 3 / span 2;
      grid-row: 3 / span 1;
    }

    &:nth-child(8) {
      background-color: orange;
      grid-column: 3 / span 2;
      grid-row: 4 / span 1;
    }
  }
}
