.aside-block {
  width: 20%;

  .aside-list {
    list-style: none;

    .aside-header {
      font-size: 2em;
      font-weight: 900;
    }

    &__item {
      text-decoration: none;
      display: flex;
      align-items: center;

      font-weight: 700;
      font-size: 16px;
      line-height: 24px;
      color: #000;

      margin: 24px 0;

      img {
        margin-right: 12px;
      }
    }
  }
}

.last {
  margin-top: 20vh;
}
