header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .menu-nav {
    width: calc(100% / 5 * 4);
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__search {
      display: flex;
      align-items: center;
    }

    &__items {
      display: flex;
      justify-content: end;
      align-items: center;

      list-style: none;
    }

    &__menu-item {
      margin-left: 12px;
      text-decoration: none;
      font-weight: 900;
      font-size: 17px;
      line-height: 22px;
      color: #000;

      border-bottom-color: transparent;
      border-bottom-style: solid;
      border-bottom-width: 2px;

      &:hover {
        padding-bottom: 4px;
        border-bottom-color: black;
      }
    }
  }
}
